body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  overflow-x: hidden;
  overflow: hidden;
  color: white;
}

.App {
  align-content: center;
  text-align: center;
  padding: 20px;
  position: relative;
  min-height: 100vh;
  background-color: rgb(176, 27, 114);
}

.content-wrapper {
  position: relative;
  z-index: 2;
}

.App-header {
  font-size: 18px;
  color: white;
  max-width: 700px;
  margin: auto;
}

.days-counter {
  margin-bottom: 20px;
  font-size: 1.1em;
  font-weight: bold;
}

.quote {
  margin-top: 40px;
  font-style: italic;
  font-size: 1em;
  color: white;
}

.floating-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
  background: pink;
}
